.viewer-container {
  text-align: center !important;
  padding: 1.5rem;
  height: 100%;
  justify-content: center !important;
  flex-direction: column !important;
  display: flex !important;
  border-radius: 10px; }
  .viewer-container.default {
    background-color: #F5F8FB;
    border: 2px dashed #0099CC;
    box-shadow: 0 0 0px 10px #F5F8FB; }
  .viewer-container.accept {
    background-color: #F5FBF7;
    border: 2px dashed #0099CC;
    box-shadow: 0 0 0px 10px #F5FBF7; }
  .viewer-container.warning {
    background-color: #FFF9E8;
    border: 2px dashed #FFBF00;
    box-shadow: 0 0 0px 10px #FFF9E8;
    color: #FFBF00; }
    .viewer-container.warning .unverified-btn {
      background-color: #FFBF00; }
  .viewer-container.invalid {
    background-color: #FBEBE7;
    border: 2px dashed #E46767;
    box-shadow: 0 0 0px 10px #FBEBE7;
    color: #E46767; }
    .viewer-container.invalid .unverified-btn {
      background-color: #E46767; }

.image-container {
  margin-bottom: 1rem; }
  .image-container img {
    height: 110px; }

.message-container {
  margin-top: 1.5rem;
  margin-bottom: 0.5rem;
  color: #000; }
  .message-container span {
    vertical-align: middle; }

.verifications {
  margin-bottom: 2rem; }
  .verifications .messages {
    font-size: 18px;
    font-weight: 600;
    margin-bottom: 0; }

.btn, .unverified-btn {
  font-weight: 400;
  text-align: center;
  vertical-align: middle;
  padding: 0.375rem 2rem;
  background-color: #0099CC;
  border: none;
  border-radius: 6px;
  min-width: 120px;
  color: #fff;
  margin: 0 auto; }

.unverified-btn {
  cursor: pointer; }
  .unverified-btn a {
    color: #fff; }

.secondary-links {
  width: 50%;
  display: flex;
  margin: 1rem auto 0 auto; }
  .secondary-links span {
    margin: auto; }
    .secondary-links span a {
      font-size: 14px;
      color: #787878 !important;
      text-decoration: underline !important; }
      .secondary-links span a:hover {
        color: #324353 !important; }
